<template>
	<view class="content">
		<view class="mask" v-if="loading" style="background-color:#F5F5F5;margin-top:0!important;" :style="{'height':loadHeight+'px!important'}">
			<view class="loading13" style="position:absolute;margin-top:-30upx;">
				<view></view>
				<view></view>
				<view></view>
				<view></view>
				<view></view>
			</view>
		</view>
		
		<view class="head-content" id="head">
			<view class="head-placeholder"></view>
			<!--分类-->
			<view class="head-class" v-if="isClass" id="headClass">
				<scroll-view class="class-scroll" scroll-x="true" :show-scrollbar="false">
					<view :class="classId == 0?'head-class-sel':''" @click="subClassSel(className, 0)">全部</view>
					<view :class="classId == c.tabId?'head-class-sel':''" 
						@click="subClassSel(className, c.tabId)" v-for="(c, index) in homeData.indexTab" :key="index">
						{{c.tabName}}
					</view>
				</scroll-view>
			</view>
			<view class="head-search">
				<!-- <image class="head-logo" :src="homeData.app_logo" mode="aspectFill"></image> -->
				<!-- #ifdef APP-PLUS -->
					<view class="head-search-center">
						<input class="head-input" @input="searchKey" @click="skipUrl('/pages/video/search', 2)" type="text" maxlength="20" placeholder="精彩内容等你来发现..." disabled="">
						<!-- <input class="head-input" @input="searchKey" type="text" maxlength="20" placeholder="精彩内容等你来发现..."> -->
						<image class="head-search-img" src="@/static/imgs/search.png" mode="aspectFill"></image>
					</view>
					<view style="display:inline-block;height:32px;line-height:32px;color:#ff0cd0;margin-top:-7upx;">
						<view class="head-search-left" style="margin-right:20upx;" @click="watchLog">
							<image class="head-history" src="@/static/imgs/log.png" mode="aspectFill"></image>
							<text>足迹</text>
						</view>
						<view class="head-search-left" style="margin-right:10upx;" @click="goDownLog">
							<image class="head-history" src="@/static/imgs/down_index.png" mode="aspectFill"></image>
							<text>下载</text>
						</view>
					</view>
				<!-- #endif -->
				
				<!-- #ifdef MP-WEIXIN || H5 -->
					<view class="head-search-center" style="width:77%;">
						<input class="head-input" @input="searchKey" @click="skipUrl('/pages/video/search', 2)" type="text" maxlength="20" placeholder="精彩内容等你来发现..." disabled="">
						<!-- <input class="head-input" @input="searchKey" type="text" maxlength="20" placeholder="精彩内容等你来发现..."> -->
						<image class="head-search-img" src="@/static/imgs/search.png" mode="aspectFill"></image>
					</view>
					<view style="display:inline-block;height:32px;line-height:32px;color:#ff0cd0;margin-top:-7upx;">
						<view class="head-search-left" style="margin-right:20upx;" @click="watchLog">
							<image class="head-history" src="@/static/imgs/log.png" mode="aspectFill"></image>
							<text>足迹</text>
						</view>
					</view>
				<!-- #endif -->
			</view>
		</view>

		<view :style="{ height : (lineLeftRight+addGdHeight) + 'px'}"></view>
		
		<!--轮播图-->
		<view class="head-banner" v-if="isBanner">
			<swiper 
				class="swiper" 
				circular interval="3000"
				previous-margin="15px"
				next-margin="15px"
				:autoplay="true" 
				:indicator-dots="true" 
				indicator-color="rgba(255,255,255,0.4)"
				indicator-active-color="rgba(255,12,208,0.8)"
				@change="bannerChange">
			    <swiper-item class="item" v-for="(item, index) in banner" :key="index" @click="jumpAdUrl(item.url)">
			        <image :src="item.images_url" class="banner" mode="aspectFill"></image>
			    </swiper-item>
			</swiper>
		</view>
		<!--公告列表-->
		<view class="head-notice" v-if="isNotice">
			<swiper class="head-notice-content" vertical="true" autoplay="false" duration="1000" interval="4000">
				<swiper-item v-for="(v, index) in homeData.notice" :key="index">
					<view @click="lookNotice(v)">
						<image class="noticeImg" src="@/static/imgs/notice.png"></image>
						<view class="head-notice-title">{{v.title}}</view>
						<view class="head-notice-time">{{utils.timeTodate('Y-m-d', v.add_time)}}</view>
					</view>
				</swiper-item>
			</swiper>
		</view>
		<!-- <view :style="{ height: addGdHeight + 'px' }" v-if="isAddHeight"></view> -->
		<!--专题集-->
		<view class="album-list" v-if="isAlbum">
			<scroll-view class="album-scroll" scroll-x="true" :show-scrollbar="false">
				<view
				class="album-content" 
				v-for="(s, i) in homeData.album" 
				:key="i"
				@click="albumPlay(s.id)">
					<view><image :src="s.img" mode="aspectFill"></image></view>
					<view class="album-name">{{s.title}}</view>
				</view>
			</scroll-view>
		</view>
		<!--标签，地区-->
		<view class="class-list">
			<view class="sub-class" v-for="(item, index) in homeData.indexClass" :key="index">
				<view class="class-type">{{item.name}}</view>
				<scroll-view class="subclass-scroll" scroll-x="true" :show-scrollbar="false">
					<view 
					:class="(tagId==c.id&&tagName==item.type)||(areaId==c.id&&areaName==item.type)?'class-sel':''" 
					v-for="(c, cindex) in item.items" 
					:key="cindex" 
					@click="subClassSel(item.type, c.id)">
					{{c.name}}
					</view>
				</scroll-view>
			</view>
			<view class="sub-class">
				<view class="class-type">排序</view>
				<scroll-view class="subclass-scroll" scroll-x="true" :show-scrollbar="false">
					<view :class="orderCode == 'lastTime' ? 'class-sel':''" @click="subClassSel('orderCode', 'lastTime')">最新</view>
					<view :class="orderCode == 'hot' ? 'class-sel':''" @click="subClassSel('orderCode', 'hot')">热门</view>
					<view :class="orderCode == 'reco'? 'class-sel':''" @click="subClassSel('orderCode', 'reco')">推荐</view>
					<view :class="orderCode == 'good'? 'class-sel':''" @click="subClassSel('orderCode', 'good')">点赞</view>
				</scroll-view>
			</view>
		</view>
		<view id="classList" style="height:1px;"></view>
		<!--视频列表-->
		<view style="width:94%;height:40px;line-height:40px;font-size:16px;color:#666666;margin:auto;" v-if="videoData.length>0">
			<image src="@/static/imgs/video3.png" style="width:32px;height:32px;margin:5px 3px 0 0;"></image>
			<view style="display:inline-block;position:absolute;">视频列表</view>
			<!-- <image src="@/static/imgs/refresh.png" style="width:20px;height:20px;margin:10px 3px 0;float:right;" @click="subClassSel('orderCode',orderCode)"></image> -->
		</view>
		<view class="video-list">
			<view class="video-info" v-for="(i, k) in videoData" :key="k">
				<view @click="jumpAdUrl(i.url)" v-if="i.adType==1">
					
					<!-- #ifndef MP-WEIXIN -->
					<easy-loadimage
						mode="aspectFill"
					    :scroll-top="scrollTop"
					    :image-src="i.content" 
					    :open-transition="true"
						class="videoImg">
					</easy-loadimage>
					<!-- #endif -->
					
					<!-- #ifdef MP-WEIXIN -->
					<image :src="i.content" mode="aspectFill" class="videoImg"></image>
					<!-- #endif -->
					
					<view class="video-playTime">广告</view>
					<view class="video-title">{{i.titles}}</view>
					<view class="video-gold">
						<!-- <image src="@/static/imgs/gold2.png" mode="aspectFill"></image>0 -->
						<image src="@/static/homePage/click.png" mode="aspectFill" style="margin-left:5px;"></image>10w+
					</view>
					<view class="video-date">{{utils.timeTodate('Y-m-d',i.add_time)}}</view>
				</view>
				<view @click="jumpAdUrl(i.content, i.adType, i.titles, i.info, i.url)" v-if="i.adType==2">
					
					<!-- #ifndef MP-WEIXIN -->
					<easy-loadimage
						mode="aspectFill"
					    :scroll-top="scrollTop"
					    :image-src="i.info" 
					    :open-transition="true"
						class="videoImg">
					</easy-loadimage>
					<!-- #endif -->
					
					<!-- #ifdef MP-WEIXIN -->
					<image :src="i.info" mode="aspectFill" class="videoImg"></image>
					<!-- #endif -->
					
					<view class="video-playTime">广告</view>
					<view class="video-title">{{i.titles}}</view>
					<view class="video-gold">
						<!-- <image src="@/static/imgs/gold2.png" mode="aspectFill"></image>0 -->
						<image src="@/static/homePage/click.png" mode="aspectFill" style="margin-left:5px;"></image>10w+
					</view>
					<view class="video-date">{{utils.timeTodate('Y-m-d',i.add_time)}}</view>
				</view>
				<view @click="playVideo(i.id, i.type)" v-if="i.adType==0">
					<!-- <image class="vip-Free" src="@/static/imgs/vip_free.png" mode="aspectFill" v-if="i.gold>0"></image> -->
					
					<!-- #ifndef MP-WEIXIN -->
					<easy-loadimage 
						mode="aspectFill"
					    :scroll-top="scrollTop"
					    :image-src="i.thumbnail"
					    :open-transition="true"
						class="videoImg">
					</easy-loadimage>
					<!-- #endif -->
					
					<!-- #ifdef MP-WEIXIN -->
					<image :src="i.thumbnail" mode="aspectFill" class="videoImg"></image>
					<!-- #endif -->
					
					<view class="video-playTime" v-if="i.play_time">{{i.play_time}}</view>
					<view class="video-title">{{i.title}}</view>
					<view class="video-gold">
						<!-- <image src="@/static/imgs/gold2.png" mode="aspectFill"></image>{{i.gold}} -->
						<image src="@/static/homePage/click.png" mode="aspectFill" style="margin-left:5px;"></image>{{i.click}}
					</view>
					<view class="video-date">{{utils.timeTodate('Y-m-d',i.update_time)}}</view>
				</view>
			</view>
		</view>
		<!--无内容-->
		<view style="width:100%;text-align:center;" v-if="isShowTig">
			<image src="@/static/imgs/nodata.png" style="width:280upx;height:180upx;"></image>
			<view style="width:100%;font-size:25upx;color:#666;margin-top:30upx;">哦豁，没有找到视频，视频正在上架中</view>
		</view>
		<view class="moreTig" v-if="isMoreTig">——— 我是有底线的 ———</view>
		<!--检测更新-->
		<view class="update" v-if="updateTig" @touchmove.stop.prevent="moveHandle">
			<view class="update-content">
				<view class="update-colse" @click="colseUpdate" v-if="!is_force_update">
					<image src="@/static/imgs/x.png" mode="aspectFill" style="width:33px;height:33px;"></image>
				</view>
				<view class="update-head">发现新版本啦</view>
				<view class="update-center" style="line-height:50upx;">
					新版本（{{newVersion}}）,升级新版本，让你的体验更流畅，功能更强大。
				</view>
				<view class="update-botton" @click="updateApp" v-if="isUpdate">立即更新<text style="font-size:12px;">{{isDown}}</text></view>
				<view v-else>
					<view class="update-ing"><view class="ing" :style="{ width : ingWidth +'%' }">{{ingWidth}}%</view></view>
					<view class="update-wz">{{updateWz}}</view>
				</view>
			</view>
		</view>
		
		<!--活动入口图标-->
		<!-- #ifndef MP-WEIXIN -->
		<view class="ad-hd" :style="{'bottom':+hdBottomHeight+h5+'px'}" v-if="homeData.isAct==1" @click="skipUrl('/pages/game/game_list', 2)">
			<image src="@/static/imgs/luck.png"></image>
		</view>
		<view class="ad-hd" v-if="isTop" :style="{'bottom':+topBottomHeight+h5+'px'}" style="right:15px;" @click="backTop">
			<image src="@/static/imgs/back-top.png" style="width:100upx;height:100upx;"></image>
		</view>
		<!-- #endif -->
		<!-- #ifdef MP-WEIXIN -->
		<view class="ad-hd" style="bottom:70px" v-if="homeData.isAct==1" @click="skipUrl('/pages/game/game_list', 2)">
			<image src="@/static/imgs/luck.png"></image>
		</view>
		<view class="ad-hd" v-if="isTop" style="right:15px;bottom:10px" @click="backTop">
			<image src="@/static/imgs/back-top.png" style="width:100upx;height:100upx;"></image>
		</view>
		<!-- #endif -->
		
		<!--自动生成账号提示-->
		<view style="position:fixed;background:rgba(0,0,0,0.5);width:100%;height:100%;top:0;left:0;z-index:998;" @click="iKnow" v-if="isKnow">
			<view style="background:#FFFFFF;width:50%;top:35%;left:25%;position:absolute;border-radius:10px;padding:20px 10px;font-size:16px;">
				<view style="color:#DD524D;border-bottom:1px solid #E8E8E8;padding-bottom:10px;margin-bottom:10px;margin-left:5px;">账号已生成，请牢记</view>
				<view style="font-size:15px;color:#666666;margin-left:5px;">账号：{{username}}</view>
				<view style="font-size:15px;color:#666666;margin-top:5px;margin-left:5px;">密码：123456</view>
				<view style="text-align:center;border-top:1px solid #E8E8E8;margin-top:10px;padding-top:10px;color:#DD524D;">我知道了</view>
			</view>
		</view>
		<!--弹出公告内容-->
		<view class="tNotice" v-if="isNoticeContent" @touchmove.stop.prevent="moveHandle">
			<view class="tNoticeContent">
				<image src="@/static/imgs/nt.png" class="tNoticeN"></image>
				<view class="tNoticeTitle">系统公告</view>
				<scroll-view scroll-y="true" class="tNoticeC">
					<!-- <rich-text :nodes="noticeContent"></rich-text> -->
					<jyf-parser :html="noticeContent" ref="article">数据正在加载中...</jyf-parser>
				</scroll-view>
				<view style="text-align:center;" @click="closeNotice">
					<view class="tNoticeColse">我知道了</view>
				</view>
				<!-- <image src="@/static/imgs/delx.png" class="tNoticeX" @click="closeNotice"></image> -->
			</view>
		</view>
		<!--弹出播放器start-->
		<view class="video" v-if="isPlay" @touchmove.stop.prevent="moveHandle">
			<view class="play-title" @click="jumpAdUrl(video.url)">{{video.videoTitle}}</view>
			<video 
				id="adVideo"
				object-fit="contain"
				:src="video.videoUrl"
				:autoplay="true"
				:controls="false"
				@timeupdate="timeupdate"
				@error="videoError"
				@ended="playEnd">
			</video>
			<view class="play-ad" @click="jumpAdUrl(video.url)">
				<text style="float:left;color:#FFFFFF;font-size:12px;">{{currentTime}}</text>
				<text>查看详情</text>
			</view>
			<view class="play-close" @click="playClose" v-if="isPlayEnd">
				<image src="@/static/imgs/delx.png"></image>
			</view>
		</view>
		<!--弹出播放器end-->
	</view>
</template>

<script>
import api from "@/common/api.js";
import easyLoadimage from '@/components/easy-loadimage/easy-loadimage.vue'
import utils from '@/components/shoyu-date/utils.filter.js';
import uniSwiperDot from "@/components/uni-swiper-dot/uni-swiper-dot.vue"
var _self, versionCode;
export default {
	components: { 
		easyLoadimage,uniSwiperDot
	},
	data() {
		return {
			// 视频广告时长秒
			currentTime: "加载中...",
			isPlayEnd: false,
			
			scrollTop: 1,
			
			homeData: [],
			banner: [],
			upDown: '../../static/imgs/_d.png',
			addGdHeight: 13,
			isClass: true,

			videoData: [],
			classId: 0,
			className: 'class',
			tagId: 0,
			tagName: 'tag',
			areaId: 0,
			areaName: 'area',
			orderCode: 'lastTime',
			where: '',
			page: 1,
			more: false,
			isSubShow: false,
			
			isAlbum: false,
			isBanner: true,
			isNotice: false,
			isNoticeContent: false,
			noticeContent: '请稍后...',
			isUpDownStatus: true,
			
			isShowTig: false,
			isMoreTig: false,
			//isAddHeight: false, // 
			search: '0',
			
			lineLeftRight: 0,
			utils: utils,
			// 更新提示
			mobileSystem: 'web',
			versionCode: '100',
			version: 'v1.0.1',
			isNew: false,
			isUpdate: true,
			updateTig: false,
			ingWidth: 0,
			updateWz: '下载中,请稍后...',
			isDown: '',
			downAppApk: false,
			downUrl: '',
			newVersion: '最新版',
			isKnow: false,
			username: '',
			firstInto: false,
			actIcon: '@/static/imgs/luck.png', 
			// 视频广告
			isPlay: false,
			video: {
				videoTitle: '加载中...',
				videoUrl: '',
				videoPoster: '',
				url: '',
			},
			context: {},
			
			is_force_update: false,
			
			bannerCurrent: 0,
			
			isTop: false,
			myScroll: 333,
			
			hdBottomHeight: 75,
			topBottomHeight: 15,
			h5: 0,
			
			loading: true,
			loadHeight: 110,
			phone: {},
			
			defaultNotice: {
				type: 1,
				content: '',
				url: ''
			},
		}
	},
	onReady() {
	   _self.context = uni.createVideoContext("adVideo", this);
	},
	onHide() {
		if(_self.isPlay) _self.context.pause();
	},
	onShow() {
		if(_self.isPlay) _self.context.play();
	},
	onLoad() {
		//uni.showLoading({ title:'加载中,请稍后...',mask:true });
		_self = this;
		_self.phone = uni.getSystemInfoSync();
		// #ifdef APP-PLUS
		// 获取手机系统 ios/android
		_self.mobileSystem = _self.phone.platform;
		_self.loadHeight = _self.phone.screenHeight;
		// #endif
		// #ifdef H5 || MP-WEIXIN
		_self.h5 = 50;
		_self.myScroll = 350;
		_self.loadHeight = _self.phone.windowHeight;
		// #endif
		_self.firstInto = uni.getStorageSync('firstInto_'+api.appkey) ? true : false;
		//console.log(_self.mobileSystem)
		// 是否有新版本
		_self.getNewApp();
		// 延迟1秒加载首页全局数据
		setTimeout(() => {
			_self.getHomeData();
		}, 800);
	},
	mounted: function() {
		const query = uni.createSelectorQuery().in(this);
		query.select('#head').boundingClientRect(ah=> {
			var toTopHeight = ah.height-1;
			_self.lineLeftRight = toTopHeight;
			//console.log(ah)
		}).exec();	
		// 获取浮动栏高度
		/* query.select('#classList').boundingClientRect(data=> {
			//_self.addGdHeight = data.height;
			_self.myScroll = data.top;
			//console.log(data);
		}).exec(); */
	},
	methods: {
		bannerChange(e) {
			//console.log(e)
			_self.bannerCurrent = e.detail.current;
		},
		moveHandle() {},
		timeupdate(e){
			var totalTime = e.detail.duration;   // 总时长
			var playsTime = e.detail.currentTime;// 播放时长
			var currentTime = parseInt(totalTime) - parseInt(playsTime);
			if(currentTime<10) currentTime = "0" + currentTime;
			_self.currentTime = currentTime+'s';
			//console.log(_self.currentTime)
		},
		playEnd() {
			_self.isPlayEnd = true;
			_self.currentTime = '00s';
			if(_self.homeData.isMustAd) _self.showHideTab(2); // 显示底部TABBAR
		},
		videoError() {
			_self.currentTime = "加载失败";
			_self.playEnd();
			_self.msgData('服务器连接失败，请稍后再试', 3000);
		}, 
		// 关闭播放器
		playClose() {
			_self.context.stop();
			_self.isPlay = false; 
			_self.video.videoUrl = '';
			_self.video.videoPoster = '';
			_self.currentTime = '加载中...';
		},
		// 我知道了
		iKnow() {
			_self.isKnow = !_self.isKnow;
			if(!_self.isKnow){
				uni.setStorage({
				    key: 'isAuto',
				    data: true
				});
			}
		},
		// 读取缓存账号
		getAutoAcc() {
			uni.getStorage({
			    key: 'isAuto',
			    success: (res)=> {
					//console.log('生成账号')
					if(!res.data){
						var row = api.getLogins();
						_self.username = row.account;
						//console.log(_self.username)
						if(_self.username != undefined){
							if(_self.username.length>0) _self.iKnow();
						}
					}
			    },
				fail: ()=> {
					//console.log('不生成账号')
				}
			});
		},
		// 进入专题
		albumPlay(e) {
			_self.skipUrl('/pages/video/album?aid='+e, 2);
		},
		// 观看历史记录
		watchLog() {
			var isLogin = api.getLogins();
			if(isLogin){
				api.jumpUrl('/pages/member/watch_log', 'new');
			}else{
				api.showToast('请先登录', 1500);
				setTimeout(()=>{
					api.jumpUrl('/pages/login/login', 'new');
				}, 1000);
			}
		},
		// 观看历史记录
		goDownLog() {
			// #ifdef H5
			api.showToast('轻量版暂不支持下载', 2000);
			// #endif
			// #ifdef APP-PLUS
			var isLogin = api.getLogins();
			if(isLogin){
				api.jumpUrl('/pages/down/down', 'new');
			}else{
				api.showToast('请先登录', 1500);
				setTimeout(()=>{
					api.jumpUrl('/pages/login/login', 'new');
				}, 1000);
			}
			// #endif
		},
		// 跳转播放页
		playVideo(vid, t) {
			//console.log(vid);
			if(t>0){
				_self.albumPlay(vid);
			}else{
				uni.setStorage({
				    key: 'vid',
				    data: vid,
					success: ()=> {
						var param = {
							url: '/pages/index/index',
							t: 'tab'
						}
						api.jumpUrl('/pages/video/play?type='+JSON.stringify(param), 'new');
					},
				    fail: ()=> {
				        api.showToast('服务器连接失败，请检查网络是否正常', 3000);
						return false;
				    }
				});
			}
		},
		// 首页数据
		getHomeData() {	
			// 检测APP是否有新版本
			_self.detectionApp();
			// 发起请求
			uni.request({
				url: api.apiData.homeData,
				method: 'POST',
				data: {
					url: api.apiUrl
				},
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e)=> {
					if(e.data.Code == 201){
						_self.msgData(e.data.Msg, 2000);
						return false;
					}
					// 首页数据
					_self.homeData = e.data.Data;
					//console.log(_self.homeData)
					// logo 
					var appLogo = _self.homeData.app_logo;
					if(appLogo.length > 0){
						api.imgCache(appLogo, (img)=>{
							_self.homeData.app_logo = img;
						});
					}else{
						_self.homeData.app_logo = '../../static/logo.png';
					}
					uni.setStorageSync('appLogo', _self.homeData.app_logo);
					// 分类
					if(_self.homeData.indexTab.length < 1) _self.isClass = false;
					// 公告
					if(_self.homeData.notice.length > 0){
						_self.isNotice = true;
						var notice = _self.homeData.defaultNotice;
						_self.noticeContent = notice.content;
						_self.defaultNotice = notice;
						//console.log(_self.notice);
						// 弹出公告
						if(!_self.updateTig&&_self.noticeContent!=null){
							_self.openNotice();
						} 
					}else{
						_self.myScroll = _self.myScroll - 50;
					}
					// 标签，地区
					if(_self.homeData.indexClass.length < 1) _self.isUpDownStatus = false; 
					//console.log(_self.isUpDownStatus)
					// 活动入口图标
					if(_self.homeData.act_icon != '') _self.actIcon = _self.homeData.act_icon;
					// 专题集
					if(_self.homeData.album.length > 0){
						_self.isAlbum = true;
					}else{
						_self.myScroll = _self.myScroll - 113;
					}
					// 轮播图start
					var banner = _self.homeData.banner;
					if(banner.length > 0){
						_self.isBanner = true;
						if(banner[0]['images_url'].length > 0){
							for(var i = 0; i < banner.length; i++) {
								api.imgCache(banner[i]['images_url'], (img)=>{
									banner[i]['images_url'] = img;
								});
							}
						}
						_self.banner = banner;
						_self.addGdHeight = 1;
					}else{ 
						_self.isBanner = false;
						_self.myScroll = _self.myScroll - 160;
					}
					//console.log(_self.isNew)
					// 加载视频
					_self.getVideoData();
				},
				fail: ()=> {
					// 加载失败提示
					_self.msgData('服务器连接失败，请检查网络是否正常', 5000);
				}
			});
		},
		// 检查版本
		detectionApp() {
			//console.log('版本检测'+_self.isNew)
			if(_self.isNew&&_self.firstInto){
				uni.getStorage({
				    key: 'newAppApk_'+ versionCode +'_'+ api.appkey,
				    success: (res)=> {
						if(res.data.code == versionCode){
							_self.downAppApk = res.data.url;
							_self.isDown = '(已下载)';
						}else{
							_self.updateApp();
							_self.downAppApk = false;
							_self.isDown = '';
						}
				    }
				});
				_self.updateTig = true;
				//_self.hideNewVer();
			}
		},
		hideNewVer() {
			uni.setStorageSync('firstInto_' + api.appkey, false);
		},
		// 隐藏或显示底部TabBar
		showHideTab(type=1){
			if(type==1){
				uni.hideTabBar({
					success: function(e){
						//console.log(e)
					}
				});
			}else{
				uni.showTabBar({
					success: function(e){
						//console.log(e)
					}
				});
			}
		},
		// 读取是否有新版本
		getNewApp() {
			// #ifdef APP-PLUS
			// 读取APP版本信息
			//_self.getAppVersion();
			plus.runtime.getProperty(plus.runtime.appid, (a) => {
				_self.versionCode = a.versionCode;
				_self.version = a.version;
				// 请求服务器版是否有新版本
				uni.request({
					url: api.apiData.getVersion,
					method: 'GET',
					header: {
						'Content-type': 'application/x-www-form-urlencoded'
					},
					success: (e)=> {
						var res = e.data.Data;
						//console.log(e)
						_self.is_force_update = res.is_force_update;
						if(_self.mobileSystem == 'ios'){
							// 苹果
							versionCode = res.apple.versionCode;
							var version = res.apple.version;
							_self.downUrl = res.apple.downUrl;
						}else{
							// 安卓
							versionCode = res.android.versionCode;
							var version = res.android.version;
							_self.downUrl = res.android.downUrl;
						}
						if(versionCode > _self.versionCode){
							// 提示有新版本
							_self.newVersion = version;
							_self.isNew = true;
						}else{
							_self.newVersion = '最新版';
							_self.isNew = false;
						}
					},
					fail: () => {
						// 加载失败提示
						_self.msgData('服务器连接失败，请检查网络是否正常', 5000);
					}
				});
			});
			// #endif
		},
		// 获取APP版本号
		getAppVersion() {
			// #ifdef APP-PLUS
			plus.runtime.getProperty(plus.runtime.appid,(e)=>{
			    //console.log(JSON.stringify(e));//应用数据
				_self.versionCode = e.versionCode;
				_self.version = e.version;
			});
			// #endif
		},
		// 在线下载
		updateApp() {
			if(_self.mobileSystem == 'ios'){
				// 苹果跳转
				_self.skipUrl(_self.downUrl, 3);
			}else{
				if(_self.downAppApk){
					_self.installApk(_self.downAppApk);
					//console.log(_self.downAppApk)
				}else{
					_self.downApp();
					//console.log(2)
				}
			}
		},
		// 下载安卓
		downApp() {
			_self.isUpdate = false;
			const downloadTask = uni.downloadFile({
			    url: _self.downUrl,
			    success: (res)=> {
			        if(res.statusCode === 200) {
			            // 保存至本地
			            uni.saveFile({
			            	tempFilePath: res.tempFilePath,
			            	success: function(red) {
								_self.updateWz = "下载完成,正在安装,请稍后...";
			            		//console.log(red.savedFilePath)
								var data = {
									'code': versionCode,
									'url' : red.savedFilePath
								};
								uni.setStorage({
								    key: 'newAppApk_'+ versionCode +'_'+ api.appkey,
								    data: data
								});
								_self.installApk(red.savedFilePath);
			            	}
			            });
			        }
			    }
			});
			downloadTask.onProgressUpdate((res) => {
			    //console.log('下载进度' + res.progress);
				_self.ingWidth = res.progress;
			});
		},
		// 关闭或显示更新弹窗
		colseUpdate() {
			_self.updateTig = false;
			_self.hideNewVer();
			if(_self.isNotice&&_self.noticeContent!=null) _self.openNotice();
		},
		// 更新应用资源
		installApk(path) {
			plus.runtime.install(path, {}, function() {
				//console.log("包装包运行成功！");
				plus.runtime.restart();
				plus.io.resolveLocalFileSystemURL(path, function(entry){
					entry.remove(function(entry) {
						//console.log("删除成功");
					}, function(e) {
						//console.log('删除失败');
					});
				});
			}, function(e) {
				//console.log(JSON.stringify(e));
				uni.setStorage({
				    key: 'newAppApk_'+ versionCode +'_'+ api.appkey,
				    data: false
				});
				_self.msgData('APP更新失败，请检查是否有权限');
				setTimeout(()=>{
					_self.colseUpdate();
				}, 1000)
			});
		},
		// 显示或隐藏标签
		isShowTab() {
			uni.showLoading({ title:'加载中,请稍后...',mask:true });
			_self.isSubShow = !_self.isSubShow;
			if(_self.isSubShow){
				_self.upDown = '../../static/imgs/_u.png';
			}else{
				_self.upDown = '../../static/imgs/_d.png';
			}
			//console.log(_self.isSubShow)
			uni.hideLoading();
		},
		// tab大分类
		getHomeClass() {
			uni.request({
				url: api.apiData.homeClass,
				method: 'GET',
				success: (e)=> {
					// 首页分类
					_self.homeClass = e.data.Data;
				},
				fail: ()=> {
					// 加载失败提示 
					_self.msgData('服务器连接失败，请检查网络是否正常', 5000);
				}
			});
		},
		// 视频列表
		getVideoData(t=0){
			if(t == 1) uni.showLoading({ title:'加载中,请稍后...',mask:true });
			//console.log(_self.where);
			uni.request({
				url: api.apiData.videoData+_self.where,
				method: 'GET',
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e) => {
					// 非加载更多状态,清空数组
					if(!_self.more){
						_self.videoData = [];
						_self.page = 1;
					} 
					//console.log(e.data.Data);
					// 我是有底线的
					if(e.data.Data.videolist.length>0){
						_self.isMoreTig = false;
						// 分类视频
						_self.videoData = _self.videoData.concat(e.data.Data.videolist);
					}else{
						_self.isMoreTig = true;
					}
					// 无数据
					if(_self.videoData.length>0){
						_self.isShowTig = false;
					}else{
						_self.isMoreTig = false;
						_self.isShowTig = true;
					}
					setTimeout(()=> {
						_self.loading = false;
						if(t==1) uni.hideLoading();
					}, 500);
				},
				fail: () => {
					// 加载失败提示
					_self.notData("服务器连接失败，请检查网络是否正常", 5000);
				}
			});
		},
		// 点击分类
		subClassSel(type, selId, more = 0) {
			//uni.startPullDownRefresh();
			//_self.more = more ? true : false;
			if(more){
				_self.more = true;
			}else{
				_self.more = false;
				_self.page = 1;
			}
			switch(type) {
			    case 'class':
					//_self.videoData = [];
			        _self.classId = selId;
			        break;
			    case 'tag':
			        _self.tagId = selId;
			        break;	
				case 'area':
				    _self.areaId = selId;
				   break;
			    default:
					_self.orderCode = selId;
			}
			_self.where='/page/'+_self.page+'/cid/'+_self.classId+'/tag_id/'+_self.tagId+'/area_id/'+_self.areaId+'/orderCode/'+_self.orderCode+'/search/'+_self.search;
			_self.getVideoData(1);
			//console.log(_self.where);
		},
		openNotice() {
			if(_self.defaultNotice.type==1){
				_self.closeNotice();
			}else{
				_self.lookNotice(_self.defaultNotice);
			} 
		},
		// 点击公告
		lookNotice(e) {
			//console.log(e)
			if(e.type == 2){
				// 跳转外部浏揽器
				api.jumpUrl(e.url, 'web');
			}else{
				uni.showLoading({ title:'加载中...',mask:true });
				_self.noticeContent = e.content;
				_self.isNoticeContent = true;
				setTimeout(()=> {
					uni.hideLoading();
				}, 500);
			}
		},
		closeNotice() {
			if(_self.firstInto){
				_self.isNoticeContent = !_self.isNoticeContent;
				uni.setStorageSync('firstInto_'+api.appkey, false);
			}else{
				_self.isNoticeContent = false;
			}
			// 获取账号
			if(!_self.isNoticeContent) _self.getAutoAcc();
		},
		// 即时搜索
		searchKey(e) {
			_self.search = e.detail.value;
			//console.log(_self.search)
			_self.subClassSel('orderCode', _self.orderCode);
		},
		// 提示
		msgData(title = '', times = 1000, style = 'none'){
			// 加载失败提示
			uni.showToast({
				icon: style,
				title: title,
				mask: true,
				duration: times
			});
		},
		// 跳转登录
		skipUrl(url = '/pages/login/login', type = 1) {
			if(type == 1){
				// 原窗口
				uni.reLaunch({ url: url });
			}else if(type == 2){
				// 新窗口
				uni.navigateTo({ url: url });
			}else{
				api.jumpUrl(url, 'web');
			}
		},
		// 跳转
		jumpAdUrl(url, type=0, title='', poster='', adUrl=''){
			if(type){
				uni.showLoading({ title:'加载中,请稍后...',mask:true });
				// 开启强制观看广告
				if(_self.homeData.isMustAd){
					_self.isPlayEnd = false;
					_self.showHideTab(1); 
				}else{
					_self.isPlayEnd = true;
				}
				_self.video.videoUrl = url;
				_self.video.videoTitle = title;
				_self.video.videoPoster = poster;
				_self.video.url = adUrl;
				_self.isPlay = true;
				setTimeout(()=> {
					uni.hideLoading();
				}, 500);
				return false;
			}
			if(url == '#' || url == '') return false;
			_self.skipUrl(url, 3); 
		},
		backTop() {
			uni.pageScrollTo({
			    scrollTop: _self.myScroll,
			    duration: 300
			});
		}
	},
	// 软键盘搜索按钮点击事件
	/* onNavigationBarSearchInputConfirmed(e){
		console.log(e);
	}, */
	// 上拉加载数据
	onReachBottom(){ 
		if(_self.isShowTig||_self.isMoreTig) return false;
		_self.page++;
		_self.subClassSel('orderCode', _self.orderCode, 1);
		//console.log(_self.page);
	},
	// 滚动监听事件
	onPageScroll({scrollTop}) {
		_self.scrollTop = scrollTop;
		if(scrollTop > _self.myScroll){
			_self.isTop = true;
		}else{
			_self.isTop = false;
		}
		//console.log(scrollTop)
	}
}
</script>
